import React, { useState, useEffect } from 'react';
import { Typography, Box, Button } from '@mui/material';

const TypingText = () => {
    const staticText = '경험으로 다듬고, 감각으로 완성하는\n';
    const dynamicText = '프론트엔드 개발자 김민정입니다.';
    const subText = '이 포트폴리오는 리액트로 만들어졌습니다.';
    const [displayText, setDisplayText] = useState('');
    const [index, setIndex] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);

    useEffect(() => {
        const interval = setInterval(() => {
            if (!isDeleting && index < dynamicText.length) {
                setDisplayText(dynamicText.substring(0, index + 1));
                setIndex(index + 1);
            } else if (isDeleting && index > 0) {
                setDisplayText(dynamicText.substring(0, index - 1));
                setIndex(index - 1);
            } else {
                setIsDeleting(!isDeleting);
            }
        }, 150);

        return () => clearInterval(interval);
    }, [index, isDeleting, dynamicText]);

    const resumeLink = `${process.env.PUBLIC_URL}/pdf/resume.pdf`;
    const scrollToProjects = () => {
        const section = document.getElementById("projects-section");
        if (section) {
            section.scrollIntoView({ behavior: "smooth" });
        }
    };

    return (
        <Box 
            sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                justifyContent: 'center', 
                position: 'relative', 
                width: '100%', 
                height: '100vh', 
                backgroundImage: `url(${process.env.PUBLIC_URL}/imgs/home.png)`,
                backgroundSize: 'contain',
                backgroundPosition: '20% bottom',
                backgroundRepeat: 'no-repeat'
            }}
        >
            <Box 
                sx={{ 
                    position: 'absolute', 
                    right: '10%', 
                    backgroundColor: 'rgba(255, 255, 255, 0.8)', 
                    padding: 3, 
                    borderRadius: '10px', 
                    boxShadow: '0 4px 10px rgba(0,0,0,0.1)',
                    textAlign: 'center'
                }}
            >
                {/* staticText와 dynamicText 크기 조정 */}
                <Typography 
                    variant="h4" 
                    sx={{ 
                        fontWeight: 'bold', 
                        whiteSpace: 'pre-line',
                        lineHeight: 1.5,
                        color: '#333',
                        fontSize: { xs: '20px', sm: '24px', md: '28px', lg: '32px' } // 반응형 텍스트 크기 조정
                    }}
                >
                    {staticText}
                    <span style={{ color: 'rgb(0 57 153)' }}>{displayText}</span>
                </Typography>
                <Typography 
                    variant="body2" 
                    sx={{ 
                        marginTop: 2, 
                        color: '#666',
                        fontSize: { xs: '12px', sm: '14px', md: '16px', lg: '18px' } // subText 크기 조정
                    }}
                >
                    {subText}
                </Typography>
                {/* 버튼 박스 - 아래로 배치 */}
                <Box sx={{ display: 'flex', gap: 2, marginTop: 2, justifyContent: 'center' }}>
                    <Button 
                        variant="contained" 
                        color="primary"
                        sx={{ backgroundColor: 'rgb(0 57 153)', color:'#fff', '&:hover': { backgroundColor: 'rgb(0 47 133)' } }}
                        href={resumeLink} 
                        target="_blank"
                    >
                        이력서 바로가기
                    </Button>
                    <Button 
                        variant="outlined" 
                        sx={{ borderColor: 'rgb(0 57 153)', color: 'rgb(0 57 153)', '&:hover': { backgroundColor: 'rgba(0, 57, 153, 0.1)' } }}
                        onClick={scrollToProjects}
                    >
                        프로젝트 확인하기
                    </Button>
                </Box>
            </Box>
        </Box>
    );
};

export default TypingText;
