import React, { useRef } from 'react';
import { Box, Typography, IconButton } from '@mui/material';
import DescriptionIcon from '@mui/icons-material/Description';
import { keyframes } from '@emotion/react';
import Header from '../layout/Header';
import TypingText from '../components/TypingText';
import ExperienceTabs from '../components/ExperienceTabs';
import ProjectsGallery from '../components/ProjectsGallery';

// 흔들리는 애니메이션 정의
const shake = keyframes`
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(5deg); }
  50% { transform: rotate(-5deg); }
  75% { transform: rotate(3deg); }
`;

const Home = () => {
    const sectionRefs = [useRef(null), useRef(null), useRef(null)];

    const scrollToSection = (index) => {
        sectionRefs[index].current.scrollIntoView({ behavior: 'smooth' });
    };

    const handleResumeClick = () => {
        window.open(`${process.env.PUBLIC_URL}/pdf/resume.pdf`, '_blank');
    };

    return (
        <Box sx={{ width: '100%', overflow: 'hidden' }}>
            <Header onNavigate={scrollToSection} />

            {/* 첫 번째 섹션 */}
            <Box
                ref={sectionRefs[0]}
                sx={{
                    minHeight: '100vh',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    backgroundColor: '#f0f0f0',
                    color: '#000',
                    position: 'relative',
                    padding: { xs: '20px', sm: '40px' }, // 작은 화면에서 여백 추가
                }}
            >
                <TypingText />
                <IconButton
                    onClick={handleResumeClick}
                    sx={{
                        position: 'fixed', // 스크롤과 상관없이 고정
                        bottom: 40,
                        right: 40,
                        animation: `${shake} 1.5s infinite`,
                        bgcolor: '#fff',
                        boxShadow: 3,
                        zIndex: 1000, // 다른 요소보다 위에 표시
                        '&:hover': {
                            bgcolor: '#e0e0e0',
                        }
                    }}
                >
                    <DescriptionIcon sx={{ fontSize: 40, color: '#333' }} />
                </IconButton>
            </Box>

            {/* 두 번째 섹션 */}
            <Box
                ref={sectionRefs[1]}
                sx={{
                    minHeight: '100vh', // 화면 크기에 맞게 최소 높이 설정
                    display: 'flex',
                    flexDirection: { xs: 'column', sm: 'row' }, // 작은 화면에서는 세로로 배치
                    alignItems: 'center',
                    justifyContent: 'center',
                    gap: 4,
                    backgroundColor: '#f5f5f5',
                    padding: 4,
                    boxSizing: 'border-box',
                }}
            >
                <Box
                    component="img"
                    src={`${process.env.PUBLIC_URL}/imgs/summer.png`}
                    alt="Profile"
                    sx={{
                        width: { xs: 200, sm: 300 },
                        height: { xs: 200, sm: 300 },
                        borderRadius: '50%',
                        boxShadow: 3,
                    }}
                />
                <Box sx={{ width: { xs: '90%', sm: '50%' } }}>
                    <ExperienceTabs />
                </Box>
            </Box>

            {/* 세 번째 섹션 */}
            <Box
                id="projects-section"
                ref={sectionRefs[2]}
                sx={{
                    minHeight: '100vh', // 최소 높이 설정
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    backgroundColor: '#fff',
                    padding: 4,
                    boxSizing: 'border-box',
                }}
            >
                <Typography variant="h4" fontWeight="bold" mb={3}>프로젝트 갤러리</Typography>
                <ProjectsGallery />
            </Box>
        </Box>
    );
};

export default Home;
